import {
  ComponentType,
  CSSProperties,
  ReactNode,
  useMemo,
  useCallback,
  useContext,
} from 'react';
import classNames from 'classnames';
import ReactSelect, {
  Props as ReactSelectProps,
  OptionTypeBase,
  MenuProps,
  components as defaultComponents,
  ValueContainerProps,
  OptionsType,
} from 'react-select';
import Async from 'react-select/async';
import CreatableSelect from 'react-select/creatable';
import AsyncCreatableSelect from 'react-select/async-creatable';

// Components
import Spinner from '../Spinner';

// Styles
import './Select.scss';

// Types
import { TestMetadata } from 'types';

// Analytics
import { AnalyticsContext, handleAnalyticsEvent } from '@dtci/dash/analytics';

export type SelectProps<T extends OptionTypeBase> = ReactSelectProps<T> & {
  testMetadata?: TestMetadata;
};

const LoadingIndicator = (): JSX.Element => {
  return <Spinner className="sb-select__loading-indicator" size="small" />;
};

const Select = <
  T extends OptionTypeBase = {
    value: string;
    label: string;
    isDisabled?: boolean;
  },
>({
  className,
  components,
  styles,
  isFetching,
  icon,
  useMenuPortal = false,
  iconContainerClassName,
  isError = false,
  testMetadata,
  flush,
  creatable,
  async,
  loadOptions,
  analyticsEventData,
  ...otherProps
}: SelectProps<T> & {
  icon?: ReactNode;
  iconContainerClassName?: string;
  useMenuPortal?: boolean;
  isError?: boolean;
  flush?: 'left' | 'right' | 'flush';
  creatable?: boolean;
  async?: boolean;
  loadOptions?: (
    inputValue: string,
    callback: (options: OptionsType<T>) => void,
  ) => void | Promise<T[]>;
}): JSX.Element | null => {
  const ValueContainer = useMemo<ComponentType<ValueContainerProps<T, false>>>(
    () =>
      icon
        ? ({ children, ...props }) => (
            <defaultComponents.ValueContainer {...props}>
              {!!children && (
                <div
                  className={classNames(
                    'sb-select__icon-container',
                    iconContainerClassName,
                  )}
                >
                  {icon}
                </div>
              )}
              <div>{children}</div>
            </defaultComponents.ValueContainer>
          )
        : defaultComponents.ValueContainer,
    [icon, iconContainerClassName],
  );

  const props: SelectProps<T> = useMemo(() => {
    return {
      isSearchable: async || creatable || false,
      tabSelectsValue: creatable || false,
      menuPortalTarget:
        useMenuPortal && typeof window !== 'undefined' ? document.body : null,
      ...otherProps,
      isLoading: isFetching,
      instanceId: otherProps.instanceId || otherProps.id,
      components: {
        LoadingIndicator,
        ValueContainer,
        ...components,
      },
      styles: {
        ...styles,
        menu: (provided: CSSProperties, state: MenuProps<T, false>) => ({
          ...provided,
          position: otherProps.expands ? 'static' : 'absolute',
          zIndex: 20,
          ...(styles?.menu && styles.menu(provided, state)),
        }),
        menuPortal: (base: CSSProperties) => ({ ...base, zIndex: 20 }),
      },
      classNamePrefix: 'sb-select',
      className: classNames(
        'sb-select',
        {
          'sb-select_error': isError,
          'sb-select__flush-right': flush === 'right',
          'sb-select__flush-left': flush === 'left',
          'sb-select__flush': flush === 'flush',
        },
        className,
      ),
    };
  }, [
    async,
    creatable,
    useMenuPortal,
    otherProps,
    isFetching,
    ValueContainer,
    components,
    styles,
    isError,
    flush,
    className,
  ]);

  const analyticsContext = useContext(AnalyticsContext);

  const handleOnChange = useCallback(
    (value, action): void => {
      props?.onChange?.(value, action);
      handleAnalyticsEvent({
        analyticsContext,
        analyticsEventData,
        componentType: 'Select',
        actionName: 'onChange',
        label:
          otherProps?.['aria-label'] ||
          otherProps?.name ||
          className ||
          testMetadata?.['data-testid'] ||
          '',
        value: [value],
      });
    },
    [
      analyticsContext,
      analyticsEventData,
      className,
      otherProps,
      props,
      testMetadata,
    ],
  );

  if (creatable) {
    if (async && loadOptions) {
      return (
        <AsyncCreatableSelect<T, false>
          {...props}
          {...testMetadata}
          loadOptions={loadOptions}
          onChange={handleOnChange}
        />
      );
    } else {
      return (
        <CreatableSelect<T, false>
          {...props}
          {...testMetadata}
          onChange={handleOnChange}
        />
      );
    }
  } else {
    if (async && loadOptions) {
      return (
        <Async<T, false>
          {...props}
          {...testMetadata}
          loadOptions={loadOptions}
          onChange={handleOnChange}
        />
      );
    } else {
      return (
        <ReactSelect<T, false>
          {...props}
          {...testMetadata}
          onChange={handleOnChange}
        />
      );
    }
  }
};

export default Select;
